@use '../base/variables' as v;
@use '../base/mixins' as m;
@use 'sass:math';

.nav-principal {
    display: flex;
    flex-direction: column;
    margin-top: 2.5rem;

    @include m.tab {
        flex-direction: row;
        gap:1rem;
        margin-top: 0;
        }

    a {
        text-align: center;
        color: v.$blanco;
        font-size: 1.8rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;
        
        @include m.tab {
            margin-bottom: 0;
        }

        &:hover {
            color: v.$secundario;
        }

        &.activo {
            color: v.$secundario;
        }
    }



    
}

