@use 'variables' as v;

/* apply a natural box layout model to all elements, but allowing components to change */
html {
    font-size: 62.5%;
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

  body {
    font-family: v.$fuente-secundaria;
    font-size: 1.6rem;
    background-color: v.$cafeClaro;    
  }

  p{
    color: v.$negro;
    font-size: 1.6rem; 
    line-height: 2rem;   
  }

  .contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
  }
  
  a {
    text-decoration: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
    display: block;   
     
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
 